import { forwardRef } from 'react';
import ReactSelectAsync, { AsyncProps } from 'react-select/async';

import { components } from './components';

type Option = { label: string; value: string };

export type MultiSelectAsyncProps = AsyncProps<Option, true, any>;

export const MultiSelectAsync = forwardRef<ReactSelectAsync, MultiSelectAsyncProps>((props, ref) => {
	return (
		<ReactSelectAsync classNamePrefix='react-select' components={components} isMulti {...props} ref={ref as any} />
	);
});
